body {
  position: relative;
  margin: 0px;
  padding: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Row--listarea {
  display: inline-block;
  width: 100%;
  height: 100%; }

.Row--list {
  display: -webkit-inline-flex;
  display: inline-flex;
  position: relative; }

.Row--list img {
  object-fit: cover;
  height: 100vh;
  width: 100vw;
  transition: all ease 0.5s; }

.Icon--info {
  position: fixed;
  z-index: 1;
  -webkit-transform: translate(94vw, 91vh) scale(1.3);
          transform: translate(94vw, 91vh) scale(1.3);
  opacity: 0.7;
  transition: all ease 0.5s; }

.Icon--info:hover {
  opacity: 1; }

.Main:hover .Icon--info {
  opacity: 0; }

.Text {
  z-index: 1;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.6);
  max-width: 20%;
  border-radius: 10px;
  -webkit-transform: translate(74vw, -55vh) scale(1);
          transform: translate(74vw, -55vh) scale(1);
  text-align: center;
  opacity: 0.8; }

.Text--Title {
  font-size: 18px; }

.Text--Content {
  font-size: 16px; }

.buttons {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 99;
  right: 25px; }
  .buttons button {
    border: none;
    background-size: contain;
    background: url("data:image/svg+xml,%3Csvg width='10' height='29' viewBox='0 0 10 29' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 0V27L1 17.4857' stroke='white' stroke-width='2' /%3E%3C/svg%3E%0A") no-repeat;
    background-position: center;
    width: 10px;
    height: 30px;
    display: block;
    margin: 20px 0;
    padding: 0 15px;
    transition-duration: .6s; }
    .buttons button.next {
      -webkit-transform: scaleY(-1);
              transform: scaleY(-1); }
    .buttons button.prev:active {
      -webkit-transform: translateY(8px);
              transform: translateY(8px); }
    .buttons button.next:active {
      -webkit-transform: scaleY(-1) translateY(8px);
              transform: scaleY(-1) translateY(8px); }

@media (max-width: 760px) {
  .Row--list img {
    height: 60vh;
    width: 100vw;
    overflow: auto; }
  .Row--list {
    display: -webkit-inline-flex;
    display: inline-flex;
    box-sizing: border-box;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    overflow-y: auto; } }

